<template>
  <div class="manageConsignee">
    <van-nav-bar
      title="收货管理"
      fixed
      @click-left="$toPage(-1)"
      @click-right="toPage('addConsignee')"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
      <span slot="right" style="font-size: 15px">新增</span>
    </van-nav-bar>
    <ul>
      <van-radio-group v-model="radio" @change="change">
        <li v-for="(item, index) in list" :key="index" @click="toOrder(item.contactname,item.contactphone)">
          <div class="uesrInfo">
            <div calss="userName">
              <span>收货人：{{ item.contactname }}</span>
              <span v-show="radio === index" class="default">默认</span>
            </div>
            <p calss="userPhone">
              收货电话：{{ item.contactphone }}
            </p>
          </div>
          <div class="defaults">
            <van-radio :name="index" >设置默认</van-radio>
            <div class="item">
              <span @click="goToConsignee(index)">编辑</span>
              <span @click="delConsignee(index)">删除</span>
            </div>
          </div>
        </li>
      </van-radio-group>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
      radio: 0,
      defaults: [],
      flag: false
    }
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo()
    }
  },
  mounted() {
    this.getConsignee()
    this.flag = this.$route.query.flag
  },
  methods: {
    toPage(name) {
      this.$router.replace({
        name: name
      })
    },
    //  获取收货人
    getConsignee() {
      JNB.query({
        serverName: 'userServer',
        cls: 'MyDrugStoreInfoModule',
        method: 'queryMyConsignee',
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
            // 更新用户登录状态
            this.list = result.data
            this.list.forEach((arr,index) => {
              if (arr.cstatus === 2) {
                this.radio = index
              }
            })
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    },
    //  更换默认
    change() {
      JNB.query({
        serverName: 'userServer',
        cls: 'MyDrugStoreInfoModule',
        method: 'setDefault',
        params: {
          compid: this.userInfo.comp.storeId,
          shipid: this.list[this.radio].shipid
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
            // 更新用户登录状态
            this.getConsignee()
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    },
    goToConsignee(i) {
      this.$router.replace({
        name: 'reviseConsignee',
        query: {
          index: i
        }
      })
    },
    //  删除
    delConsignee(i) {
      if(this.list[i].cstatus !== 2){
        JNB.query({
          serverName: 'userServer',
          cls: 'MyDrugStoreInfoModule',
          method: 'delConsignee',
          params: {
            shipid: this.list[i].shipid
          },
          callback: (data)=>{
            let result = JSON.parse(data)
            if (result.code === 200) {
              // 更新用户登录状态
              this.getConsignee()
            }else {
              this.$toast.fail(result.message)
            }
          }
        })
      }else{
        this.$toast('不能删除默认选项！')
      }
    },
    toPage() {
      this.$router.push({
        name: 'addConsignee'
      })
    },
    //  订单选择收货人
    toOrder(name,tel) {}
  }
}
</script>
<style scoped>
.manageConsignee{
  background-color: #f8f8f8;
  padding-top: 6.4vh;
  box-sizing: border-box;
}
.manageConsignee ul li{
  width: 660px;
  background-color: #fff;
  margin: 20px auto 20px;
  border-radius: 20px;
  padding: 30px 30px;
  overflow: hidden;
}
.manageConsignee .uesrInfo .default{
  float: right;
  margin-right: 20px;
  font-size: 20px;
  color: rgba(249,30,30,1);
  background-color: rgb(247, 196, 204);
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  line-height: 100%;
}
.manageConsignee .defaults .van-radio{
  float: left;
  font-size: 24px;
}
.manageConsignee .defaults .item{
  float: right;
}
.manageConsignee .defaults .item span{
  margin-right: 20px;
  font-size: 24px;
}
</style>


